.page-header {
  width: 100%;
  min-height: 950px;
  background-image: url("../img/bg-desktop@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  background-color: $dark;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/bg-desktop@2x.png");
  }

  @media (max-width: 1023px) {
    background-image: url("../img/bg-tablet@1x.png");
    min-height: 685px;
  }

  @media (max-width: 1023px) and (min-resolution: 2dppx) {
    background-image: url("../img/bg-tablet@2x.png");
  }

  @media (max-width: 767px) {
    background-image: url("../img/bg-mobile@1x.png");
    min-height: 647px;
  }

  @media (max-width: 767px) and (min-resolution: 2dppx) {
    background-image: url("../img/bg-mobile@2x.png");
  }
}

.page-header__wrapper {
  width: 100%;
  padding: 20px 7% 0 7%;
  display: flex;
  flex-direction: column;
}

.page-header__info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  padding: 0;
  margin: 0 0 300px 0;

  @media (max-width: 1023px) {
    margin: 0 0 150px 0;
  }
}

.page-header__logo img {

  @media (max-width: 767px) {
    width: 38px;
    height: 38px;
  }
}

.page-header__contacts {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 75%;
  justify-content: flex-end;

  @media (max-width: 1023px) {
    width: auto;
  }
}

.page-header__contacts a {
  display: block;
  cursor: pointer;
}

.page-header__contacts a:last-child {
  margin-left: 38px;

  @media (max-width: 1023px) {
    display: none;
  }
}

.page-header__contacts a:nth-child(0n+2) {
  margin-left: 78px;

  @media (max-width: 1023px) {
    margin-left: 25px;
  }

  @media (max-width: 767px) {
    margin-left: 20px;
  }
}

.page-header__link {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.page-header__link svg {
  margin-right: 15px;

  @media (max-width: 1023px) {
    margin-right: 0;
  }
}

.page-header__link--email svg {

  @media (max-width: 1023px) {
    width: 26px;
    height: 19px;
  }
}

.page-header__link--phone svg {

  @media (max-width: 1023px) {
    width: 25px;
    height: 24px;
  }
}

.page-header__link span {
  color: $white;
  font-size: 16px;
  line-height: 18px;

  @include ptmono;

  @media (max-width: 1023px) {
    display: none;
  }
}

.page-header__order-call {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: $white;
  text-align: center;
  padding: 9px 23px;
  border: 1px solid $white;
  border-radius: 5px;

  @include roboto-bold;
}
